import styled from 'styled-components'
import { variables } from '../../styles/variables';

export const Ruling = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  background-image: url(${props => props.image? props.image : ''});
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  font-size: 16px;
  .ruling {
    padding: 35px;
    color: white;
     &__title {
      position: relative;
      &__state {
        position: absolute;
        left: -35px;
        top: 9px;
        width: 40px;
        height: 40px;
      }
      &__name {
        font-size: 3em;
        font-weight: 600;
      }
    }

    &__experience {
      font-size: 14px;
      font-weight: 600;
      margin: 0px;
    }

    &__message {
      font-size: 18px;
      font-weight: 200;
      margin: 10px 0px;
    }

    &__actions {
      width: 45%;
      display: grid;
      grid-gap: 10px;
      grid-template-columns: 1fr 1fr 3fr;
    }

  }
  .message {
    flex: auto;
    text-align: left;
    font-size: 15px;
    font-weight: 200;
  }
  .close {
    cursor: pointer;
    width: 30px;
    height: 30px;
  }
`;





